import { Component, OnInit } from '@angular/core';
// importation des animations qu'on va utiliser 
import { trigger,style,transition, animate, keyframes,query,stagger} from '@angular/animations';
//
import { DataService } from '../data.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css'],
  // add to components those animations
  animations:[
    trigger('goals',[
      transition('* => *',[
        query(':enter', style({ opacity:0}), {optional: true}),

        query(':enter', stagger('300ms', [
          animate('.6s ease-in', keyframes([
            style({opacity: 0, transform :'translateY(-75%)', offset:0}),
            style({opacity: .5, transform :'translateY(35px)', offset:.3}),
            style({opacity: 1, transform :'translateY(0)', offset:1})
          ]))]), {optional: true}),
          query(':leave', stagger('300ms', [
            animate('.6s ease-in', keyframes([
              style({opacity: 1, transform :'translateY(0)', offset:0}),
              style({opacity: .5, transform :'translateY(35px)', offset:.3}),
              style({opacity: 0, transform :'translateY(0)', offset:1})
            ]))]), {optional: true})
      ])
    ])
  ]
})
export class HomeComponent implements OnInit {

  itemCount: number = 4;
  btnText : string = 'Ajouter but';
  goalText : string = 'Mon objectif...';
  goals = ['I want to by an Audi','I want to travel to europe']; // array;

  constructor(private _data: DataService) { }

  ngOnInit() {
    this._data.goal.subscribe(res => this.goals = res);
    this.itemCount =  this.goals.length;
    this._data.chnageGoal(this.goals);
  }
  addItem(){
    this.goals.push(this.goalText);
    this.goalText = '';
    this.itemCount = this.goals.length;
    this._data.chnageGoal(this.goals);
  }

  removeItem(i){
    this.goals.splice(i, 1);
    this._data.chnageGoal(this.goals);
  }

}
